<template>
    <div class="content centered padding-15">
        <div class="pannel">
            <router-link :to="{ name: 'ProjectPage', params: { id: index } }">
                <div class="header is-clipped fixed is-alert">   
                    {{ name }} 
                </div>
            </router-link>
            <div class="pannel-img">
                <img :src="image">
            </div>
            <div class="body"> 
                <p id="text">{{ description }}</p>
            </div>
            <router-link :to="{ name: 'ProjectPage', params: { id: index } }">
                <button class="button is-info">Project details</button>
            </router-link>
        </div>
    </div>
</template>

<script>
export default {
    name: 'project',
    props: ['index', 'name', 'description', 'link', 'image'],
    data () {
        return {
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.content {
    max-width: 25%;
}

a {
    text-decoration: none;
}

.body {
    color: #000;
}

#text {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.button {
    margin-top: 10px;
    margin-bottom: 20px;
    cursor: pointer;
}

.is-clipped {
    transition: width 1s ease-in;
    cursor: pointer;
}

.pannel {
    transition: box-shadow 0.5s ease-in;
}

.content:hover .is-clipped {
    width: 80%;
}

.content:hover .pannel {
    box-shadow: 0 2px 20px 7px rgba(90, 89, 89, 0.8);
}


</style>




