<template>
    <div class="about">
        <div id="first-line" class="container flex-row">
            <div class="content centered col-1">
                <h1>{{ projects[id].name }} project</h1>
            </div>
        </div>
        
        <div class="container flex-row">
            <div id="float" class="content centered col-1">
                <div class="box with-shadow bg-white">

                    <div class="container">
                        <div class="content centered">
                            <img class="project-image" :src="projects[id].image"> 
                        </div>
                    </div>

                    {{ projects[id].description }}

                    <div class="container">
                        <div class="content centered">
                            <a :href="projects[id].link"><button class="button is-info">Go to project</button></a>
                        </div>
                    </div>

                    <div class="container">
                        <div class="content centered">
                            <social_buttons></social_buttons>
                        </div>
                    </div>

                </div>
            </div>         
        </div>

        <buttom-footer></buttom-footer>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
    name: 'project_page',
    props: ['id'],
    data () {
        return {
        }
    },
    computed: {
        ...mapGetters({
            projects: 'getProjects',
        })
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.box {
    padding-left: 100px;
    padding-right: 100px;
    padding-bottom: 50px;
}

.border {
    border-left: 2px solid black;
    padding-left: 30px;
}

.exp-block {
    margin-top: 50px;
}

#first-line {
    margin-top: -20px;
    background-color: #2f82ff;
    padding-top: 50px;
    padding-bottom: 100px;
}

#float {
    max-width: 70%;
    position: relative;
    top: -150px;
}

h1 {
    text-transform: uppercase;
    color: #fff;
    margin-bottom: 50px;
    text-align: center;
}

h1#black-title {
    color: #000;
    opacity: 0.7;
}

h3 {
    margin-bottom: 50px;
    margin-top: 50px;
    padding: 0px;
    text-align: center;
    color: #2f82ff;
}


</style>



