export const getProjects = function(state) {
    return state.projects
}

export const getSpecificProject = function(state, { id }) {
    console.log(id)
    for(var i = 0; i < state.projects.length; i++)
    {
        if(state.projects[i].name == 'AutomaticPost')
        {
            console.log(state.projects[i])
            return state.projects[i]
        }
    }
}




