<template>
    <div>
        <div id="navigation" class="navigation">
            <div class="nav-items left">
                <router-link class="nav-item" :to="{ name: 'HomePage' }" exact-active-class="is-active" active-class="">Home</router-link>
                <router-link class="nav-item" :to="{ name: 'AboutPage' }" exact-active-class="is-active" active-class="">About</router-link>
                <router-link class="nav-item" :to="{ name: 'ContactPage' }" exact-active-class="is-active" active-class="">Contact</router-link>
            </div>
            <div class="nav-mobile-items">
                <span id="nav-mobile-item" class="nav-mobile-item" onclick="showDropdown('dropdown_1');">Mobile</span>
            </div>

            <div class="nav-items right">
                <a class="nav-item" href="https://www.facebook.com/blebea.serban"><i class="icon fa fa-facebook-square" aria-hidden="true"></i></a>
                <a class="nav-item" href="form.php#nav_item_0"><i class="icon fa fa-linkedin-square" aria-hidden="true"></i></a>
                <a class="nav-item" href="form.php#nav_item_2"><i class="icon fa fa-github" aria-hidden="true"></i></a>
            </div>
        </div>

        <div class="nav-mobile-dropdown">
            <ul>
                <li><a href="">Home</a></li>
                <li><a href="">About</a></li>
                <li><a href="">Contact</a></li>
                <li><a onclick="hideDropdown();">Close</a></li>
            </ul>
        </div>
    </div>
</template>

<script>

export default {
    name: 'navigation',
    data () {
        return {
        }
    },
    mounted() {
        document.onscroll = function() {
            if (window.scrollY >= 80) {
                document.getElementById('navigation').className = "navigation is-hidden";
            } else {
                document.getElementById('navigation').className = "navigation";
            }
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.nav-items.left {
    margin-left: 65px;
}

.nav-items.right {
    margin-right: 65px;
}

.icon {
    font-size: 21px;
    padding: 0;
}

</style>



